  align-items: center;
  flex-basis: 30%;
  text-align: center;
  margin-top: 10%;
  max-width: 50%;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(58.333333333333336% - 30px);
}
@media screen and (max-width: 480px) {
  .main__video__hero {
    width: calc(100% - 30px);
  }
}
@media screen and (max-width: 768px) {
  .main__video__hero {
    width: calc(100% - 30px);
  }
}
@media screen and (max-width: 992px) {
  .main__video__hero {
    width: calc(66.66666666666667% - 30px);
  }
}
@media screen and (max-width: 1200px) {
  .main__video__hero {
    width: calc(100% - 30px);
  }
}
@media screen and (max-width: 1200px) {
  .main__video__hero {
    max-width: 80%;
  }
}
@media screen and (max-width: 992px) {
  .main__video__hero {
    max-width: 98%;
  }
}
@media screen and (max-width: 768px) {
  .main__video__hero {
    max-width: 98%;
  }
}
.main__video__hero h1 {
  color: white;
  font-size: 2.7rem;
}
.main__video__hero h2 {
  color: white;
  font-size: 1.7rem;
  font-weight: 300;
}
.main__video__hero a {
  padding: .5rem 2rem;
  border: 1px solid white;
  color: #222;
  background: white;
  transition: all .3s;
  box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.1);
  border-radius: .2rem;
  margin-top: 3rem;
}
.main__video__hero a:hover {
  color: white;
  background: #222;
  border-color: #222;
  transform: translateY(-2px);
}
.main__video__hero a:hover a {
  color: white;
}
a.mouse-next {
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translateX(-50%);
  animation: mouse-move 1.5s ease-in-out infinite;
  color: #fff;
}
@media screen and (max-width: 992px) {
  a.mouse-next {
    display: none;
  }
}
a.mouse-next i {
  font-size: 2.5rem;
  color: white;
  padding: 1rem;
}
a.mouse-next i:before {
  animation: mouse-fade 1.5s ease-in-out infinite;
  color: white;
}
@keyframes mouse-move {
  from {
    bottom: 10%;
  }
  to {
    bottom: 5%;
  }
}
@keyframes mouse-fade {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
